<template>
  <div class="">
    <h4>系统配置</h4>
    <el-divider></el-divider>
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="130px"
      class="demo-ruleForm mt-30"
      size="small"
      style="width: 30%"
    >
      <el-form-item label="是否开启记录存储" prop="is_record">
        <el-switch
          v-model="ruleForm.is_record"
          :active-value="1"
          :inactive-value="0"
        ></el-switch>
      </el-form-item>
      <el-form-item label="存储时间(天)" prop="save_duration">
        <el-input-number
          controls-position="right"
          v-model="ruleForm.save_duration"
          :min="1"
        ></el-input-number>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"
          >确 定</el-button
        >
      </el-form-item>
    </el-form>
    <h4>系统重启</h4>
    <el-divider></el-divider>
    <div class="d-flex f-center">
      <el-button type="primary" size="small" @click="restart">重启</el-button>
      <div class="ml-10 f-bold fs-14">重新启动设备</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        save_duration: 7,
        is_record: 0,
      },
      rules: {
        save_duration: [{ required: true, message: "请输入", trigger: "blur" }],
      },
    };
  },
  created() {
    this.getData();
  },
  methods: {
    restart() {
      this.$confirm("是否重启系统?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$api.post("/settting/restartSystem").then(() => {
            this.$message.success("操作成功");
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消",
          });
        });
    },
    getData() {
      this.$api.get(`/settting/getSysSetting`).then((res) => {
        if (res.data.data) {
          this.ruleForm = res.data.data;
        }
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$api
            .post("/settting/updateSysSetting", this.ruleForm)
            .then(() => {
              this.$message.success("操作成功");
              this.getData();
            });
        } else {
          return false;
        }
      });
    },
  },
};
</script>

<style></style>
